
.signupFormComp{
    width: 60%;
    margin: auto;
    height: auto;
  }
  
  .signupForm{
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      height: 80%;
      margin: 7px;     
  }
  
  .signupForm input{
      border-radius : 35px;
      border : 1.5px solid #E70B89;
      height: 50px;
      margin-top: 15px;
  }
  
  ::placeholder{
     padding-left: 20px;
     font-weight: 600; 
     font-size: 16px;
     color: #1A1A1A;
     opacity: 0.56;
  }
  
  .signupForm button{
      all: unset;
      height: 50px;
      background-color: #E70B89;
      border-radius: 35px;
      color: white;
      margin-top: 30px;
      cursor: pointer;
  } 
                    
                     
